export const Point = (x, y) => ({ x, y });
const abs = (i) => (i < 0 ? -i : i);
const ADJACENT = [Point(1, 0), Point(0, 1), Point(-1, 0), Point(0, -1)];
export const PointHelper = {
    isPositive: (p) => p.x >= 0 && p.y >= 0,
    isZero: (p) => p.x === 0 && p.y === 0,
    area: (p) => p.x * p.y,
    getPointsAround: (p) => {
        const diff = [-1, 0, 1];
        const points = new Array(9);
        diff.forEach((i) => {
            diff.forEach((j) => {
                const q = Point(i, j);
                if (!PointHelper.isZero(q))
                    points.push(PointHelper.add(p, q));
            });
        });
        return points;
    },
    getAdjacentPoints: (p) => ADJACENT.map((q) => PointHelper.add(p, q)),
    add: (p, q) => Point(p.x + q.x, p.y + q.y),
    subtract: (p, q) => Point(p.x - q.x, p.y - q.y),
    abs: (p) => Point(abs(p.x), abs(p.y)),
    isAround: (p, q) => {
        const diff = PointHelper.abs(PointHelper.subtract(p, q));
        return PointHelper.isInBounds(diff, Point(1, 1));
    },
    isInBounds: (p, bound) => PointHelper.isPositive(p) && p.x < bound.x && p.y < bound.y,
};
